export const help = {
    help_01: "Eine rückwirkende Befristung ist nicht zulässig, sondern würde zu einem unbefristeten Arbeitsverhältnis führen. Daher muss das Anfangsdatum in der Zukunft liegen und noch hinreichend Zeit für die Unterzeichnung des Vertrags durch beide Vertragspartner gegeben sein.",
    help_02: "Das Werkstudentenprivileg in der gesetzlichen Sozialversicherung setzt grundsätzlich voraus, dass der Student nicht mehr als 20 Stunden in der Woche arbeitet. Soll er mehr als 20 Stunden wöchentlich arbeiten, besteht das Werkstudentenprivileg grundsätzlich nur dann, wenn das Arbeitsverhältnis auf maximal 3 Monate befristet ist.",
    help_03: "",
    help_04: "Mit der vertraglichen Vereinbarung zu der „Berufsbeschreibung“ oder der „Position“ des Studenten sind Sie automatisch berechtigt, auf der Basis des arbeitgeberseitigen Weisungsrechts die einzelnen Aufgaben und Tätigkeiten zuzuweisen, die im Rahmen dieser Berufsbeschreibung oder Position üblicherweise anfallen. Es empfiehlt sich für ein kündigungsgeschütztes Arbeitsverhältnis nicht, weitergehend detaillierte Stellenbeschreibungen oder Aufgaben vertraglich zu vereinbaren, weil jede Änderung dazu dann nur einvernehmlich oder durch Änderungskündigung zulässig ist.",
    help_05: "Die Festlegung eines Ortes im Vertrag ist nicht zwingend. Die Benennung des Ortes hat zur Folge, dass Sie dem Werkstudenten nur bei zusätzlicher Vereinbarung einer örtlichen Versetzungsklausel einseitig einen anderen Ort als Arbeitsort zuweisen können. Es ist daher regelmäßig sinnvoll, die Stadt / Kommune und keinen Stadtteil und keine Betriebsstätte zu nennen.",
    help_06: "Eine solche Klausel kann empfehlenswert sein, wenn Sie sich nicht dauerhaft auf einen bestimmten Arbeitsort festlegen wollen. Die Klausel ermöglicht zudem auch den Einsatz an wechselnden Arbeitsorten wie beispielsweise Montagearbeiten. Soll der Student auch im Home-Office tätig werden, empfiehlt es sich je nach Art der Tätigkeit, zusätzliche Details der Home-Office-Tätigkeit durch weitere, hier nicht abgebildete Vereinbarungen zu regeln, etwa wie die Arbeitszeit erfasst werden soll, ob sich der Arbeitgeber an den Kosten des privaten Telefon- bzw. Internetanschlusses beteiligt oder welche Maßnahmen durchzuführen sind, damit die Datensicherheit gewährleistet ist.",
    help_07: 'Diese Information ist entscheidend dafür, wie viele Urlaubstage dem Studenten nach dem Gesetz zustehen. Bitte ermitteln Sie den Durchschnitt der Arbeitstage pro Woche und runden Sie Tage an denen nicht voll gearbeitet wird auf. Für einen halben Tag geben Sie in diesem Fall nicht "0,5", sondern "1" an.',
    help_08: 'Arbeitnehmer dürfen grundsätzlich nicht an Sonn- oder Feiertagen beschäftigt werden. Zwingende gesetzliche Regeln verbieten grundsätzlich die Beschäftigung an Sonntagen. Nur in besonderen, aufgelisteten Ausnahmefällen ist die Sonn- und Feiertagsbeschäftigung zulässig (vgl. v. a. § 10 und § 13 Arbeitszeitgesetz), andernfalls liegt eine Ordnungswidrigkeit oder ggf. sogar eine Straftat vor.  Ausnahmen gelten unter anderem für Gastronomiebetriebe, im Bewachungsgewerbe oder auch für Verkehrsbetriebe. Ob für Ihr Unternehmen eine Ausnahme vorliegt, ist im Einzelfall zu prüfen und kann hier nicht beurteilt werden.',
    help_09: 'Arbeitgeber sind nach dem Mindestlohngesetz (MiLoG) verpflichtet, Arbeitnehmern einen zwingenden gesetzlichen Mindestlohn in jeweils gesetzlich festgelegter Höhe, derzeit von EUR 12 brutto pro Stunde, zu zahlen. Die vertragliche Vereinbarung zur Höhe der Vergütung muss den Bestimmungen des MiLoG entsprechen. Auch bei der Angabe einer monatlichen Vergütung ist darauf zu achten, dass der daraus errechenbare Stundensatz die Grenze von EUR 12 brutto nicht unterschreitet. Eine zu niedrige Vergütungsvereinbarung macht die Vergütungsvereinbarung insgesamt unwirksam und es gilt dann die übliche Vergütung als vereinbart. Diese kann im Einzelfall deutlich über dem Mindestlohn liegen.',
    help_10: "Arbeitnehmer dürfen grundsätzlich nicht an Sonn- oder Feiertagen beschäftigt werden. Zwingende gesetzliche Regeln verbieten grundsätzlich die Beschäftigung an Sonntagen. Nur in besonderen, aufgelisteten Ausnahmefällen ist die Sonn- und Feiertagsbeschäftigung zulässig (vgl. v. a. § 10 und § 13 Arbeitszeitgesetz), andernfalls liegt eine Ordnungswidrigkeit oder ggf. sogar eine Straftat vor.  Ausnahmen gelten unter anderem für Gastronomiebetriebe, im Bewachungsgewerbe oder auch für Verkehrsbetriebe. Ob für Ihr Unternehmen eine Ausnahme vorliegt, ist im Einzelfall zu prüfen und kann hier nicht beurteilt werden.",
    help_11: 'Nach Ihren Angaben hat der Student keinen Anspruch auf Mindestlohn. Die Eingabe eines Stundenlohns von unter EUR 12 ist somit zulässig. Sie sollten jedoch wissen, dass eine fehlerhafte Einschätzung zum Mindestlohnanspruch risikobehaftet ist: Hat der Student doch einen Mindestlohnanspruch, ist die zu niedrige Vergütungsvereinbarung unwirksam und es gilt die übliche Vergütung als vereinbart. Diese kann im Einzelfall deutlich über dem Mindestlohn liegen. Unter "Das heißt genau" erfahren Sie, wann der Stundenlohn sogar als sittenwidrig anzusehen ist.',
    help_12: "Angeordnete und geleistete Überstunden müssen grundsätzlich entweder entsprechend des Mindestlohngesetzes vergütet oder durch Freizeit ausgeglichen werden; nur in engen Grenzen ist eine Pauschalabgeltung mit dem Monatsgehalt zulässig. Der Arbeitgeber hat jedoch ein Wahlrecht, auf welche Weise er Überstunden kompensieren möchte. Für eine rechtssichere Gestaltung sollten Sie eine der beiden ausdrücklichen Alternativen zur Kompensation von Überstunden wählen. Auch bei einer Vergütung von Überstunden dürfen aber nicht die Höchstgrenzen des Arbeitszeitgesetzes überschritten werden. Um den Anforderungen des Mindestlohngesetzes zu entsprechen, darf die Vergütung der Überstunden 12 € brutto nicht unterschreiten.",
    help_13: "Sie können als Arbeitgeber eine Sonderleistung zahlen, deren Höhe Sie jedes Jahr neu festsetzen. Die Höhe der Zahlung wird so in das Ermessen des Arbeitgebers gestellt. Der Vertrag sieht im Übrigen nicht vor, dass der Student ein 13. Monatsgehalt, Weihnachtsgeld, Urlaubsgeld oder ähnliches erhält.",
    help_14: "Das Bundesurlaubsgesetz sieht einen Anspruch auf mindestens 24 Tage Urlaub pro Kalenderjahr vor, bezieht diesen Mindestwert aber auf die Werktage von Montag bis Samstag (ohne Sonn- und Feiertage), somit auf eine Sechs-Tage-Arbeitswoche. Der gesetzliche Mindesturlaub beträgt daher vier Arbeitswochen und dieser Mindestwert ist zur Darstellung in „Urlaubstagen“ jeweils bezogen auf die vertraglichen (regelmäßigen) Arbeitstage pro Woche umzurechnen. Für einen Arbeitnehmer mit einer Fünf-Tage-Woche ergibt sich ein Mindesturlaub von 20 Tagen, für einen Arbeitnehmer mit einer Vier-Tage-Woche ein Mindesturlaub von 16 Tagen usw. Entsprechend ist auch bei der Festlegung von zusätzlichen Urlaubstagen zu rechnen.",
    help_15: "In dem Fall wird der Vertrag auf diese Richtlinie verweisen.",
    help_16: "Ein ausdrückliches Verbot ist empfehlenswert, um für den Studenten Transparenz zu schaffen. Andernfalls kann durch die Privatnutzung durch den Studenten bzw. die Duldung dieser Privatnutzung durch den Arbeitgeber schnell ein Privatnutzungsrecht entstehen. Daher muss das Unternehmen ein Privatnutzungsverbot auch stichprobenartig kontrollieren und durchsetzen, damit es auch in Zukunft weiterhin gilt. Sie sollten sich ggf. von einem Rechtsanwalt zu den zulässigen Kontrollmaßnahmen beraten lassen.",
    help_17: "Gesetzlich ist der Student verpflichtet, Ihnen eine solche Bescheinigung vorzulegen, wenn seine Krankheit drei Kalendertage andauert. Dann muss er an dem darauffolgenden Arbeitstag die ärztliche Arbeitsunfähigkeitsbescheinigung vorlegen. Sie dürfen früher eine Arbeitsunfähigkeitsbescheinigung fordern.",
    help_18: "Für die ersten sechs Monate kann für jedes Arbeitsverhältnis eine ordentliche Kündigungsfrist von zwei Wochen vereinbart werden. Diese Vereinbarung der kürzesten zulässigen Kündigungsfrist wird gesetzlich als Probezeitvereinbarung bezeichnet.",
    help_19: "Die gesetzliche Kündigungsfrist beträgt vier Wochen zum 15. oder zum Ende eines Kalendermonats. Hat das Arbeitsverhältnis zwei Jahre bestanden, verlängert sich die gesetzliche Kündigungsfrist für den Arbeitgeber stufenweise gemäß § 622 BGB. Dies sind zwingende Mindestfristen für den Arbeitgeber. Sie können vertraglich aber längere Kündigungsfristen vereinbaren. Der Vertrag wird in dem Fall vorsehen, dass die vertraglich vereinbarten, längeren Fristen auch für den Studenten gelten.",
    help_20: "Die gesetzliche Frist für eine ordentliche Kündigung des Studenten beträgt vier Wochen zum 15. oder zum Ende eines Kalendermonats. Diese Frist verlängert sich im Gegensatz zur Frist für die Kündigung des Arbeitgebers nicht gesetzlich. Es ist aber zulässig, die Verlängerungen, die für die ordentliche Kündigung durch den Arbeitgeber gelten, auch für die ordentliche Kündigung durch den Studenten für anwendbar zu erklären.",
    help_21: "Als Organisation werden unter anderem Gesellschaften (KG, OHG, GbR, UG, AG, GmbH u.a.), eingetragene Vereine (e.V.) und eingetragene Kaufleute (e.K.) verstanden",
    help_22: "",
    help_23: "",
    help_24: "",
    help_1401:""

};

